
export const validateForm = (values) => {
    const regexp = /[^A-Za-z]+/g;
    const errors = {};

    if (!values.userName) {
        errors.userName = ' ';
    } else if (regexp.test(values.userName)) {
        errors.userName = ' !';
    }

    if (!values.phone) {
        errors.phone = '  ';
    }

    if (!values.message) {
        errors.message = ' ';
    }
    return errors;
}
